.contactus-body {
	min-height: 100vh;
	width: 100%;
	background: #c8e8e9;
	display: flex;
	align-items: center;
	justify-content: center;
}
.contactus-container {
	width: 85%;
	background: #fff;
	border-radius: 6px;
	padding: 20px 60px 30px 40px;
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
.contactus-container .contactus-content {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.contactus-container .contactus-content .contactus-left-side {
	width: 25%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-top: 15px;
	position: relative;
}
.contactus-content .contactus-left-side::before {
	content: "";
	position: absolute;
	height: 70%;
	width: 2px;
	right: -15px;
	top: 50%;
	transform: translateY(-50%);
	background: #afafb6;
}
.contactus-content .contactus-left-side .details {
	margin: 14px;
	text-align: center;
}
.contactus-content .contactus-left-side .details i {
	font-size: 30px;
	color: #3e2093;
	margin-bottom: 10px;
}
.contactus-content .contactus-left-side .details .topic {
	font-size: 18px;
	font-weight: 500;
}
.contactus-content .contactus-left-side .details .text-one,
.contactus-content .contactus-left-side .details .text-two {
	font-size: 14px;
	color: #afafb6;
}
.contactus-container .contactus-content .contactus-right-side {
	width: 75%;
	margin-left: 75px;
}
.contactus-content .contactus-right-side .topic-text {
	font-size: 23px;
	font-weight: 600;
	color: #3e2093;
}
.contactus-right-side .input-box {
	height: 50px;
	width: 100%;
	margin: 12px 0;
}
.contactus-right-side .input-box input,
.contactus-right-side .input-box textarea {
	height: 100%;
	width: 100%;
	border: none;
	outline: none;
	font-size: 16px;
	background: #f0f1f8;
	border-radius: 6px;
	padding: 0 15px;
	resize: none;
}
.contactus-right-side .message-box {
	min-height: 110px;
}
.contactus-right-side .input-box textarea {
	padding-top: 6px;
}
.contactus-right-side .button {
	display: inline-block;
	margin-top: 12px;
}
.contactus-right-side .button input[type="button"] {
	color: #fff;
	font-size: 18px;
	outline: none;
	border: none;
	padding: 8px 16px;
	border-radius: 6px;
	background: #3e2093;
	cursor: pointer;
	transition: all 0.3s ease;
}
.button input[type="button"]:hover {
	background: #5029bc;
}

@media (max-width: 950px) {
	.contactus-container {
		width: 90%;
		padding: 30px 40px 40px 35px;
	}
	.contactus-container .contactus-content .contactus-right-side {
		width: 75%;
		margin-left: 55px;
	}
}
@media (max-width: 820px) {
	.contactus-container {
		margin: 40px 0;
		height: 100%;
	}
	.contactus-container .contactus-content {
		flex-direction: column-reverse;
	}
	.contactus-container .contactus-content .contactus-left-side {
		width: 100%;
		flex-direction: row;
		margin-top: 40px;
		justify-content: center;
		flex-wrap: wrap;
	}
	.contactus-container .contactus-content .contactus-left-side::before {
		display: none;
	}
	.contactus-container .contactus-content .contactus-right-side {
		width: 100%;
		margin-left: 0;
	}
}
/*Contact Us CSS end*/

